import React from "react";
import "./Jumbotron.css";

const Jumbotron = () => (
  <div className="jumbotron text-center">
    <a href="/"><h1>Adoptable</h1></a>
    <h4>
      Pet adoption made easy.
    </h4>
  </div>
);

export default Jumbotron;



